* {
    text-decoration: none;
    font-size : 60%;
    overflow: hidden;
    background-color: black;
    color : white;
    width : 100%;
   
   
  }
  
  header {
    position : fixed; 
    top : 0; 
    left : 0; 
    width : 100%;
    padding : 4rem 12% 4rem; 
    background : rgba(0, 0, 0, 0.3); 
    backdrop-filter: blur(10px);
    display : flex; 
    justify-content: space-between;
    align-items: center;
    z-index: 5;
   
  }
  
  nav {
    text-align : center;
    margin-right : 15rem;
    padding-bottom : 3px;
    
  }
  
  nav a {
    font-size: 1.5rem;
    margin-left : 3rem;
    font-weight : 500;
    transition: 0.3s ease;
    border-bottom : 3px solid transparent; 
    
  }
  
  nav a:hover, 
  nav a.active {
    color : #00ffee; 
    border-bottom : 3px solid #00ffee;
  }
  
  .logo {
    font-size : 3rem;
    font-weight: 800;
    cursor : pointer; 
    transition: 0.3s ease;
  }
  
  .logo:hover {
    transform : scale(1.1); 
  }
  
  .logo span {
    text-shadow : 0 0 25px gray; 
  }
  
  section {
    min-height : 100vh; 
    padding : 10rem 12% 10rem; 
   }
  
   .home {
    display: flex;
    align-items: center;
    justify-content: center;
    gap : 15rem; 
   }
  
   .home-content {
    display: flex;
    flex-direction: column;
    align-items: baseline;
    text-align: left;
    justify-content: center;
    margin-top : -15rem; 
   }
  
   span {
    color : #00ffee; 
  
   }
  
   .logo span {
    color : #00ffee; 
   }
  
   .home-content h3 {
    margin-bottom: 2rem;
    margin-top: -2rem;
    font-size: 3rem;
   }
  
   .home-content h1 {
    font-size: 7rem;
    font-weight: 700;
    /* margin-top: 1.5rem; */
    
    line-height: 1;
    
    overflow: hidden;
   }
  .home-img {
  
    margin-bottom : 25vh;
    margin-left : 2vw;
    margin : auto;
   
  }
  
   .home-img img {
    position: relative;
    top : 3rem; 
    width : 25vw;
    height : 25vw; 
    border-radius: 50%;
    box-shadow : 0 0 25px #00ffee; 
    cursor : pointer; 
    transition: 0.4s ease-in-out;
    margin-bottom : 30vh;
    margin-left : 2vw;
  
   }
  
   .home-img img:hover{
    box-shadow : 0 0 25px #00ffee, 0 0 50px #00ffee;
  
  
   }
  
   .profile {
    position : relative;
    
    
    
   }
  
   .HomeProfile {
    position : relative; 
    top : -100px;
   }
  
   .home-content p {
    font-size : 1.5rem; 
    font-weight: 500;
    line-height: 1.8;
    max-width: 1000px;
   }
  .social-icons {
    padding-left : 2rem;
  }
  
   .boxicons i{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width : 4.5rem; 
    height : 4.5rem; 
    background : transparent; 
    border : 2px solid #00ffee; 
    font-size : 2.5rem;
    border-radius: 50%;
    color : #00ffee; 
    margin : 3rem 1.5rem 3rem 0; 
    transition: 0.3s ease-in-out;
  
   }
  
  
  
  
   
  
   .boxicons i:hover{
    color : #00ffee; 
    transform : scale(1.3)translateY(-5px); 
    box-shadow: 0 0 25px #00ffee;
    background-color: black;
  }
  
  
  
  
  .btn {
    display: inline-block;
    padding : 1rem 2.8rem; 
    background : #00ffee; 
    box-shadow: 0 0 25px #00ffee;
    border-radius: 4rem;
    font-size: 1.6rem;
    color : black; 
    border : 2px solid transparent; 
    letter-spacing: 0.1rem;
    font-weight: 600;
    transition: 0.3s ease-in-out;
    cursor : pointer; 
  }
  
  .btn:hover {
    transform: scale(1.05);
    box-shadow : 0 0 50px #00ffee; 
  
  }
  
  .btn-group {
    display: flex;
    align-items: center;
    gap : 1.5rem;
  }
  
  .social-icons:nth-of-type(2){
    background-color: black;
    color : #00ffee; 
    border : 2px solid #00ffee; 
    box-shadow: 0 0 25px transparent;
  
  }
  
  .social-icons:nth-of-type(2):hover{
    box-shadow: 0 0 25px #00ffee;
    background-color: #00ffee;
    color : black; 
  
  }
  
  .text-animation {
    font-size : 34px; 
    font-weight: 600;
    min-width : 280px; 
  
  }
  
  
  
  .text-animation span {
    position : relative; 
  }
  
  .text-animation span::before{
    content: "Web Developer";
    color : #00ffee; 
    animation: words 20s infinite; 
  
  }
  
  
  @keyframes cursor {
    to{
      border-left : 2px solid #00ffee; 
    }
  }
  
  @keyframes words {
    0%, 
    33%{
      content : "오예준"; 
    }
    34%, 
    66%{
      content : "이재경"; 
    }
    67%, 
    100%{
      content : "김태훈";
    }
   
  }
  