.Contact-container { 


}

.sectionContainer {
    display : flex;
}

.Contact-form {
  width : 35vw; 
  height : 60%;
 border: none;
  margin-top : 5vh;
  margin-left : 7vw; 



}

.Contact-form-title {
    
    font-size : 3vw;
    color : white;
    font-weight: bold;
    background-color: #D3D3D3;
    width : 18vw;
}

.Contact-form-inputContainer {
    width : 30vw;
    background-color: black;
    
   
}

.Contact-form-input {
    margin-top : 3vh;
    margin-bottom : 3vh;
    background-color: #D3D3D3;
    width : 25vw;
    height : 5vh;
    border : 1px solid #D3D3D3;
    border-radius: 2px;
    outline: none;
    padding-left : 0.5vw;
    
}

.Contact-form-email {
   
    margin-bottom : 7vh;
    background-color: #D3D3D3;
    width : 25vw;
    height : 5vh;
    border : 1px solid #D3D3D3;
    border-radius: 2px;
    outline: none;
    padding-left : 0.5vw;
}

.Contact-form-content {
    
    
    background-color: #D3D3D3;
    width : 25vw;
    height : 25vh;
    border : 1px solid #D3D3D3;
    border-radius: 2px;
    outline: none;
    padding-left : 0.5vw;

    
}

.Contact-form-content::placeholder {
    font-weight: bold;
    
}
/* 
.Contact-form-content::placeholder {
    text-align: start;
} */

.contact-submit {
    margin-top : 5vh;
    width : 25.7vw;
    height : 4vh;
    border : 1px solid white;
    background-color: #D3D3D3;
    border-radius: 2px;
    cursor : pointer;
    color : gray;
    
}



.Geo {
    cursor : pointer;
    position : relative;
    top : -55vh;
    width : 40%;
    height : 180vh;
}