.geometry {
    position : relative; 
    top : -15vh;
    left : -5vw;
}

.Text {
    position : relative;
    top : -20vh;
    right : 10vw;
}

@keyframes moveText {
    to {
        width : 100%;
    }
}

.reactTitle {
    font-size : 5vw;
    font-weight: bold;
    cursor : pointer; 
    color : transparent;
    -webkit-text-stroke : 1px white;
    position : relative;
    margin-bottom : 15vh;

    
}

.reactTitle::after {
    content: "React";
    position : absolute; 
    top : 0; 
    left : 0; 
    color : #00eeff; 
    width : 0px; 
    overflow : hidden; 
    white-space : nowrap; 
}


.reactTitle:hover::after {
    
        animation: moveText 0.5s linear both;
        
     
    
}

