@keyframes modalFadeIn {
    from {
        opacity: 0;
        transform: scale(0.8);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes modalFadeOut {
    from {
        opacity: 1;
        transform: scale(1);
    }
    to {
        opacity: 0;
        transform: scale(0.8);
    }
}

@keyframes slideLeft {
    from {
        transform: translateX(100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}


@keyframes slideRight {
    from {
        transform: translateX(0);
        opacity: 1;
    }
    to {
        transform: translateX(100%);
        opacity: 0;
    }
}

.modal {
    top: 0; 
    left: 0; 
    position: fixed;
    width: 100vw; 
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
}

.modal.closing {
    animation: modalFadeOut 0.3s forwards;
}

.modal-container {
    width: 50%; 
    background-color: #D3D3D3;
    border-radius: 10px;
    position: relative;
    padding: 20px;
    opacity: 0;
    transform: scale(0.8);
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.modal-container.open {
    opacity: 1;
    transform: scale(1);
    animation: modalFadeIn 0.3s forwards;
}

.modal-container.close {
    opacity: 0;
    transform: scale(0.8);
    animation: modalFadeOut 0.3s forwards;
}

.modal-container.left {
    animation : slideLeft 0.3s forwards; 
   
}

.modal-container.right {
   animation: slideRight 0.3s forwards;
}

.modal-closeButton-container {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #D3D3D3;
    width: 30px;
    height: 30px;
}

.modal-closeButton-container i {
    font-size: 30px;
    cursor: pointer;
    width: 30px;
    height: 30px;
    background-color: #D3D3D3;
}

.modal-title {
    color: black; 
    font-size: 24px;
    text-align: center;
    margin-bottom: 20px;
    background-color: #D3D3D3;
    font-weight: bold;
}

.reactPortfolioVideo {
    display: flex;
    justify-content: center;
}

.arrow-icon {
    width : 40px;
    position: absolute;
    top: 50%;
    left: 15vw; /* 화살표 위치 조정 */
    transform: translateY(-50%);
    z-index: 1000; /* 모달 내부 요소 위로 */
    cursor: pointer;
}

.bx-chevron-left {
  
    font-size: 40px;
    color: #D3D3D3;
    
    
}

.arrow-icon-right {
    width : 40px;
    position: absolute;
    top: 50%;
    right: 15vw; /* 화살표 위치 조정 */
    transform: translateY(-50%);
    z-index: 1000; /* 모달 내부 요소 위로 */
    cursor: pointer;
}


.bx-chevron-right {
  
    font-size: 40px;
    color: #D3D3D3;
    
}
